import {Injectable} from '@angular/core';
import * as introJs from 'intro.js/intro';

@Injectable({
  providedIn: 'root'
})
export class IntroService {

  introJS = introJs();

  constructor() {
  }

  //   ()   
  public startIntroJS() {

    this.introJS.setOptions(
      {
        nextLabel: '. >',
        prevLabel: '< .',
        doneLabel: '',
        skipLabel: '',
        exitOnEsc: true,
        exitOnOverlayClick: false
      });

    this.introJS.start();

  }

}
